<script>
import EmptyBoard from '@/components/board/Empty.vue';
import Board from '@/components/board/Board.vue'
import bgOverlay from '@/components/bgOverlay.vue';
import TaskView from '@/components/manager/TaskView.vue'
import TaskForm from '@/components/manager/TaskForm.vue';
import Delete from '@/components/manager/Delete.vue';
import BoardForm from '@/components/manager/BoardForm.vue';
import SideBar from '@/components/manager/Sidebar.vue';
import SidebarMobile from '@/components/manager/SidebarMobile.vue';
import ShowSidebar from '@/components/manager/sidebar/ShowSidebar.vue';
import IconBoard from '@/components/icons/IconBoard.vue';

import { mapState } from "vuex";

import { useBoardsStore } from '@/store/boards.js';
import { useManagerStore } from '@/store/manager.js';
import NoBoards from '@/components/board/NoBoards.vue';
import AddNewColumn from '@/components/board/AddNewColumn.vue';
import Dropdown from '@/components/manager/Dropdown.vue';
import IconArrowDown from '@/components/icons/IconArrowDown.vue';
import IconArrowUp from '@/components/icons/IconArrowUp.vue';
import PrimarySmall from '@/components/buttons/PrimarySmall.vue';
import AddButtonMobile from '@/components/buttons/AddTaskMobile.vue'
import ManagementService from "@/services/management.service";

export default {
  name: "Management",
  data() {
    return {
      content: "",
      boardsStore: useBoardsStore(),
      managerStore: useManagerStore(),
    };
  },
  async mounted() {
    console.log(this.currentUser);

    this.boardsStore.$subscribe((mutations, state) => {
      localStorage.setItem('data', JSON.stringify(state))
    })
    const storageData = localStorage.getItem("data")
    // console.log(storageData);

    if (storageData === null) {
      await ManagementService.getManagementByUser(this.currentUser).then(
        (response) => {
          console.log(response);
          this.boardsStore.boards = response;
          console.log(this.boardsStore.boards);

        },
        (error) => {
          this.content =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
        }
      );
      // const jsonData = await import("@/assets/json/data.json")
      // console.log(jsonData);
      // this.boardsStore.boards = jsonData.boards;
      // console.log(this.boardsStore.boards);
    } else {
      this.boardsStore.$state = JSON.parse(storageData)
    }

    this.$nextTick(function () {
      this.$refs.target?.scrollIntoView();
    });

  },
  methods: {
    addTask: function () {
      this.managerStore.overlay = true
      this.managerStore.taskForm = { edit: false, visible: true }
    },
    createNewBoard: function () {
      this.managerStore.overlay = true
      this.managerStore.boardForm = { visible: true, edit: false }
    },
    editTask: function () {
      this.managerStore.overlay = true
      this.managerStore.boardForm = { edit: true, visible: true }
    },
    deleteTask: function () {
      this.managerStore.overlay = true
      this.managerStore.delete = { board: true, visible: true }
    },
    showMobileBar: function () {
      this.managerStore.sidebarMobile = true;
      this.managerStore.overlay = true
    },
    
  },
  components: {
    TaskView,
    TaskForm,
    Delete,
    BoardForm,
    SidebarMobile,
    EmptyBoard,
    bgOverlay,
    NoBoards,
    Board,
    Dropdown,
    AddButtonMobile,
    PrimarySmall
  },
  computed: {
    ...mapState(["isRTL", "isAbsolute"]),
    currentUser() {
      return this.$store.state.auth.user;
    },
  },

};

</script>

