<script>
import FullCalendar, { CalendarOptions, EventApi, DateSelectArg, EventClickArg } from '@fullcalendar/vue3'
import dayGridPlugin from '@fullcalendar/daygrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import interactionPlugin from '@fullcalendar/interaction'
import { INITIAL_EVENTS, createEventId } from '@/services/utils/calendar-utils'
import moment from 'moment'

import UserService from "@/services/user.service";
import AuthService from "@/services/auth.service";

import Breadcrumbs from "@/components/Breadcrumbs.vue";
import Toast from "@/components/Toast.vue";
import AOS from "aos";
import { mapState } from "vuex";


import { useBoardsStore } from '@/store/boards.js';
import { useManagerStore } from '@/store/manager.js';


export default {
  name: "Profile",
  data() {
    return {
	  content: "",
	  boardsStore: useBoardsStore(),
      managerStore: useManagerStore(),
	  
	  user: {},
	  accountPassword: {
		username: "",
		original: "",
		password: "",
		confirm: ""
	  },

	  calendarOptions: {
        plugins: [
          dayGridPlugin,
          timeGridPlugin,
          interactionPlugin // needed for dateClick
        ],
        headerToolbar: {
          left: 'prev,next today',
          center: 'title',
          right: 'dayGridMonth,timeGridWeek,timeGridDay'
        },
        initialView: 'timeGridWeek',
        initialEvents: INITIAL_EVENTS, // alternatively, use the `events` setting to fetch from a feed
        editable: true,
        selectable: true,
        selectMirror: true,
        dayMaxEvents: true,
        weekends: true,
        select: this.handleDateSelect,
        eventClick: this.handleEventClick,
        eventsSet: this.handleEvents
        /* you can update a remote database when these fire:
        eventAdd:
        eventChange:
        eventRemove:
        */
      },
      currentEvents: []

    };
  },
  mounted() {
	AOS.init();

	// this.boardsStore.$subscribe((mutations, state) => {
    //   localStorage.setItem('data', JSON.stringify(state))
    // })
    // const storageData = localStorage.getItem("data")
    // if (storageData === null) {
    //   const jsonData = import("@/assets/json/data.json")
    //   this.boardsStore.boards = jsonData.boards;
    // } else {
    //   this.boardsStore.$state = JSON.parse(storageData)
    // }

	this.$nextTick(function () {
		this.$refs.target?.scrollIntoView();
	});
	UserService.findUserByUserName(this.currentUser.username).then(
      (response) => {
        this.user = response.data;
      },
      (error) => {
        this.content =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString();
      }
	);
  },
  methods: {
	yyyymmdd(date) {
		return moment(date).format('YYYY-MM-DD');
    },
	updateUser() {
		console.log(this.user);
		UserService.updateUser(this.user).then(
			(response) => {
				this.content = "user updated.";
				this.$refs.toastRef.startShow();
			},
			(error) => {
				this.content =
				(error.response &&
					error.response.data &&
					error.response.data.message) ||
				error.message ||
				error.toString();
			}
		);
	},
	changePassword() {
		this.accountPassword.username = this.user.username;
		AuthService.update(this.accountPassword).then(
			(response) => {
				this.content = "password updated.";
				this.$refs.toastRef.startShow();
				this.accountPassword = {
					username: "",
					original: "",
					password: "",
					confirm: ""
				}
			},
			(error) => {
				this.content =
				(error.response &&
					error.response.data &&
					error.response.data.message) ||
				error.message ||
				error.toString();
			}
		);
	},
	handleWeekendsToggle() {
      this.calendarOptions.weekends = !this.calendarOptions.weekends // update a property
    },
    handleDateSelect(selectInfo) {
      let title = prompt('Please enter a new title for your event')
      let calendarApi = selectInfo.view.calendar
      calendarApi.unselect() // clear date selection
      if (title) {
        calendarApi.addEvent({
          id: calendarId++,
          title,
          start: selectInfo.startStr,
          end: selectInfo.endStr,
          allDay: selectInfo.allDay
        })
      }
    },
    handleEventClick(clickInfo) {
      if (confirm(`Are you sure you want to delete the event '${clickInfo.event.title}'`)) {
        clickInfo.event.remove()
      }
    },
    handleEvents(events) {
      this.currentEvents = events
    }

  },
  components: {
	Breadcrumbs,
	Toast,
	FullCalendar
  },
  computed: {
    ...mapState(["isRTL", "isAbsolute"]),

	currentUser() {
		return this.$store.state.auth.user.user;
