<script>
import UserService from "@/services/user.service";
import LandingService from "@/services/landing.service";
import * as constant from '@/assets/js/constant.js'

import Breadcrumbs from "@/components/Breadcrumbs.vue";
import AOS from "aos";
import axios from 'axios';
import { mapState } from "vuex";

export default {
  name: "Contact",
  data() {
    return {
	  content: "",
	  users: {},
	  organization: {},
    };
  },
//   computed: {
//     ...mapState(["isRTL"]),
//   },
  mounted() {
	AOS.init();
	this.$nextTick(function () {
		window.scrollTo(0, 0);
		// this.$refs.target?.scrollIntoView();
	});
    UserService.getPublicContent().then(
      (response) => {
        this.content = response.data;
      },
      (error) => {
        this.content =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString();
      }
	);
	UserService.getUsers().then(
      (response) => {
		this.users = response.data;
		if(this.users.length > 0) this.organization = this.users[0];
		this.organization.optime = this.organization.optime.split(',');
      },
      (error) => {
        this.content =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString();
      }
	);
	this.organization.map = "https://www.google.com/maps/embed/v1/place?q=place_id:ChIJ65anG27SwoARTkmc4Fc_odA&key=AIzaSyBkW3r7uPOfcD9A0VugdO9NWY2WBd7064k"
  },
  methods: {
	selectedUserRegister: function (user) {
		this.organization = user;
		// this.organization.optime = user.optime.split(',');
    },

  },
  components: {
	Breadcrumbs
  },
  computed: {
    ...mapState(["isRTL", "isAbsolute"]),

    currentRouteName() {
      	return this.$route.path.substring(1);
    },
  },

};
</script>