import IconCross from '@/components/icons/IconCross.vue';
import BaseRadioButtonGroup from "@/components/form/BaseRadioButtonGroup.vue";

import ButtonPrimaryLarge from '@/components/buttons/PrimaryLarge.vue';
import ButtonSecondaryLarge from '@/components/buttons/SecondaryLarge.vue';

const boardsStore = useBoardsStore();
const managerStore = useManagerStore();

const inputTitle = ref(null)
const inputs = ref([])

const decisions = reactive(["Yes", "No", "Undecided"])
const selected = reactive("")

const board = reactive({
        name: '',
				age: '',
				sex: '',
				address: '',
				city: '',
				state: '',
				zipCode: '',
				homePhone: '',
				cellPhone: '',
				email: '',
				birthDate: '',
				occupation: '',
				ssn: '',
				referring: '',
				eName: '',
				eRelationShip: '',
				ePhone: '',

				insuFirst: '',
				insuTwo: '',
				insuThree: '',
				insuFour: '',
				insuFive: '',
				insuSix: '',
  columns: []
})
const columnsPlaceholders = {
  0: 'e.g. Todo...',
  1: 'e.g. Doing...',
  2: 'e.g. Done...'
}
// const userSaidYes = {
//   return selected === "Yes";
// }
// const userSaidNo = {
//       return selected === "No";
// }
// const userIsUndecided = {
//   return selected === "Undecided";
