<template>
  <section class="relative pt-16 pb-32 flex content-center items-center justify-center">
		<div class="container mx-auto px-1 lg:px-4">
			<div class="flex flex-wrap justify-end text-center">
				<Breadcrumbs :currentPage="currentRouteName" />
			</div>
			<div class="flex flex-wrap items-center mt-16">
				<div class="w-full md:w-12/12 px-4 mr-auto ml-auto">

          <div class="card card-container">
            <img
              id="profile-img"
              src="//ssl.gstatic.com/accounts/ui/avatar_2x.png"
              class="profile-img-card"
            />
            <Form @submit="handleRegister" :validation-schema="schema">
              <div v-if="!successful">
                <div class="form-group">
                  <label for="username">Username</label>
                  <Field name="username" type="text" class="form-control input input-bordered w-full max-w-xs" />
                  <ErrorMessage name="username" class="error-feedback" />
                </div>
                <!-- <div class="form-group">
                  <label for="email">Email</label>
                  <Field name="email" type="email" class="form-control" />
                  <ErrorMessage name="email" class="error-feedback" />
                </div> -->
                <div class="form-group">
                  <label for="password">Password</label>
                  <Field name="password" type="password" class="form-control input input-bordered w-full max-w-xs" />
                  <ErrorMessage name="password" class="error-feedback" />
                </div>

                <div class="form-group mt-3">
                  <button class="btn btn-primary btn-block" :disabled="loading">
                    <span
                      v-show="loading"
                      class="spinner-border spinner-border-sm"
                    ></span>
                    Sign Up
                  </button>
                </div>
              </div>
            </Form>

            <div
              v-if="message"
              class="alert"
              :class="successful ? 'alert-success' : 'alert-danger'"
            >
              {{ message }}
            </div>
          </div>
          </div>
          </div>
  </div>
  </section>
</template>

<script>
import Breadcrumbs from "@/components/Breadcrumbs.vue";
import { Form, Field, ErrorMessage } from "vee-validate";
import * as yup from "yup";

export default {
  name: "Register",
  components: {
    Form,
    Field,
    ErrorMessage,
    Breadcrumbs,
  },
  data() {
    const schema = yup.object().shape({
      username: yup
        .string()
        .required("Username is required!")
        .min(3, "Must be at least 3 characters!")
        .max(20, "Must be maximum 20 characters!"),
      // email: yup
      //   .string()
      //   .required("Email is required!")
      //   .email("Email is invalid!")
      //   .max(50, "Must be maximum 50 characters!"),
      password: yup
        .string()
        .required("Password is required!")
        .min(6, "Must be at least 6 characters!")
        .max(40, "Must be maximum 40 characters!"),
    });

    return {
      successful: false,
      loading: false,
      message: "",
      schema,
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    },
    currentRouteName() {
      let paths = this.$route.path.split("/");
      return paths[1];
    },
  },
  mounted() {
    if (this.loggedIn) {
      this.$router.push("/profile");
    }
  },
  methods: {
    handleRegister(user) {
      this.message = "";
      this.successful = false;
      this.loading = true;

      this.$store.dispatch("auth/register", user).then(
        (data) => {
          this.message = data.message;
          this.successful = true;
          this.loading = false;
        },
        (error) => {
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.successful = false;
          this.loading = false;
        }
      );
    },
  },
};
</script>

